import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
//页面
import { DataAuthoManageComponent } from './data-authority-manage.component';//数据权限管理
import { BusinessRole } from './business-role/business-role.component';//业务角色管理
import { FieldManage } from './field-manage/field-manage.component';//字段管理
import { FieldGroup } from './field-group/field-group.component';//字段组管理
