import { Component, OnInit } from '@angular/core';
import { MenubarModule, MenuItem } from 'primeng/primeng';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
	
//数据权限管理
@Component({
  selector: 'app-data-authority-manage',
  templateUrl: './data-authority-manage.component.html',
  styleUrls: ['./data-authority-manage.component.scss'],
  providers: [Commfunc]
})
export class DataAuthoManageComponent implements OnInit {

  // items: MenuItem[];
  items1: MenuItem[];
  items2: MenuItem[];
  items3: MenuItem[];
  show:number;
  constructor(
    public commfunc:Commfunc
  ) { }

  ngOnInit() {
    this.items1=[{
      label: '业务角色管理',
          routerLink:'/pages/tzb/back-manage/base-manage/data-authority/business-role'
    }]
    this.items2=[{
      label: '页面权限管理',
        routerLink:'/pages/tzb/back-manage/base-manage/data-authority/page-permission-manage'
    }]
    this.items3=[{
      label: '数据权限',
        routerLink:'/pages/tzb/back-manage/base-manage/data-authority/data-manage'
    }]
  this.judgStatu()
  }
  // changeTab($event){
  // }
  judgStatu () {
    let url = window.location.href;
    let urlSplit = url.split('/');
    urlSplit.map(v => {
      if (v === 'business-role') {
        this.show = 1;
      }else if (v === 'page-permission-manage') {
        this.show = 2;
      } else if (v === 'data-manage') {
        this.show = 3;
      }
    })
  }
apply(num) {
    this.show = num;
  }
//按钮权限
permissionCheck(id){
  return	this.commfunc.permissionCheck(id)
  }
}
